@import "../../variables/colors.scss";
@import "../../styles/index.scss";

.unit-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.draggable {
  cursor: move;
  transition: opacity;
}

.draggable:hover {
  opacity: 0.5;
}

.star-container {
  display: inline-flex;
  justify-content: center;
  margin-bottom: -8px;
  z-index: 1;
  filter: drop-shadow(0px 3.28515px 10.5855px rgba(0, 0, 0, 0.262193))
    drop-shadow(0px 1.59488px 5.13905px rgba(0, 0, 0, 0.21139))
    drop-shadow(0px 0.781839px 2.51926px rgba(0, 0, 0, 0.16861))
    drop-shadow(0px 0.30914px 0.996119px rgba(0, 0, 0, 0.117807));
  min-height: 24px;
  min-width: 100%;
  z-index: 2;
}

.star-container-medium {
  display: inline-flex;
  justify-content: center;
  margin-bottom: -6px;
  z-index: 1;
  filter: drop-shadow(0px 3.28515px 10.5855px rgba(0, 0, 0, 0.262193))
    drop-shadow(0px 1.59488px 5.13905px rgba(0, 0, 0, 0.21139))
    drop-shadow(0px 0.781839px 2.51926px rgba(0, 0, 0, 0.16861))
    drop-shadow(0px 0.30914px 0.996119px rgba(0, 0, 0, 0.117807));
  min-height: 16px;
  min-width: 100%;
  z-index: 2;
}

.star-container-small {
  display: inline-flex;
  justify-content: center;
  margin-bottom: -4px;
  z-index: 1;
  filter: drop-shadow(0px 3.28515px 10.5855px rgba(0, 0, 0, 0.262193))
    drop-shadow(0px 1.59488px 5.13905px rgba(0, 0, 0, 0.21139))
    drop-shadow(0px 0.781839px 2.51926px rgba(0, 0, 0, 0.16861))
    drop-shadow(0px 0.30914px 0.996119px rgba(0, 0, 0, 0.117807));
  min-height: 12px;
  min-width: 100%;
  z-index: 2;
}

.star-container img {
  height: 24px;
  z-index: 2;
  cursor: pointer;
}

.star-container-medium img {
  height: 16px;
  z-index: 2;
}

.star-container-small img {
  height: 12px;
  z-index: 2;
}

.start-not-active {
  display: none;
  cursor: pointer;
}

.unit-hex-container-medium:hover .start-not-active {
  display: flex;
}

.unit-image {
  border: 3px solid $divider;
  border-radius: 4px;
  background-color: $background07;
  width: 86px;
  height: 86px;
}

.unit-image-medium {
  border: 2px solid $divider;
  border-radius: 3px;
  background-color: $background07;
  width: 72px;
  height: 72px;
}

.unit-image-small {
  border: 1px solid $divider;
  border-radius: 2px;
  background-color: $background07;
  width: 42px;
  height: 42px;
}

.item-container {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  z-index: 2;
  gap: 7px;
  margin-top: -8px;
  filter: drop-shadow(0px 3.28515px 10.5855px rgba(0, 0, 0, 0.262193))
    drop-shadow(0px 1.59488px 5.13905px rgba(0, 0, 0, 0.21139))
    drop-shadow(0px 0.781839px 2.51926px rgba(0, 0, 0, 0.16861))
    drop-shadow(0px 0.30914px 0.996119px rgba(0, 0, 0, 0.117807));
}

.item-container-medium {
  display: inline-flex;
  flex-direction: row;
  gap: 6px;
  margin-top: -6px;
  filter: drop-shadow(0px 3.28515px 10.5855px rgba(0, 0, 0, 0.262193))
    drop-shadow(0px 1.59488px 5.13905px rgba(0, 0, 0, 0.21139))
    drop-shadow(0px 0.781839px 2.51926px rgba(0, 0, 0, 0.16861))
    drop-shadow(0px 0.30914px 0.996119px rgba(0, 0, 0, 0.117807));
}

.item-container-small {
  display: inline-flex;
  flex-direction: row;
  gap: 2px;
  margin-top: -4px;
  filter: drop-shadow(0px 3.28515px 10.5855px rgba(0, 0, 0, 0.262193))
    drop-shadow(0px 1.59488px 5.13905px rgba(0, 0, 0, 0.21139))
    drop-shadow(0px 0.781839px 2.51926px rgba(0, 0, 0, 0.16861))
    drop-shadow(0px 0.30914px 0.996119px rgba(0, 0, 0, 0.117807));
}

.item-container img {
  height: 24px;
  width: 24px;
  border-radius: 2px;
}

.item-container-medium img {
  height: 20px;
  width: 20px;
  border-radius: 1px;
}

.item-container-small img {
  height: 12px;
  width: 12px;
  border-radius: 1px;
}

.grey-border {
  border: 3px solid $unit1;
}

.green-border {
  border: 3px solid $unit2;
}

.blue-border {
  border: 3px solid $unit3;
}

.purple-border {
  border: 3px solid $unit4;
}

.yellow-border {
  border: 3px solid $unit5;
}

.hexagon {
  width: 98px;
  height: 112px;
  overflow: hidden;
  visibility: hidden;
  -webkit-transform: rotate(120deg);
  -moz-transform: rotate(120deg);
  -ms-transform: rotate(120deg);
  -o-transform: rotate(120deg);
  transform: rotate(120deg);
}

.hexagon-in1 {
  overflow: hidden;
  width: 100%;
  height: 100%;
  -webkit-transform: rotate(-60deg);
  -moz-transform: rotate(-60deg);
  -ms-transform: rotate(-60deg);
  -o-transform: rotate(-60deg);
  transform: rotate(-60deg);
}
.hexagon-in2 {
  width: 100%;
  height: 100%;
  background-position: 50%;
  background-size: 112px;
  background-color: $background07;
  visibility: visible;
  -webkit-transform: rotate(-60deg);
  -moz-transform: rotate(-60deg);
  -ms-transform: rotate(-60deg);
  -o-transform: rotate(-60deg);
  transform: rotate(-60deg);
}

.hex-border {
  width: 104px;
  height: 118px;
  position: absolute;
  top: 0px;
  left: -3px;
}

.hex-image {
  z-index: 1;
}

.hex-border .hexagon-in2 {
  background-color: $divider;
}

.unit-hex-container {
  position: relative;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  max-height: 95px;
  min-height: 95px;
}

.unit-hex-container .star-container {
  margin-bottom: -17px;
  min-height: 20px;
  z-index: 2;
}

.unit-hex-container .star-container img {
  height: 20px;
  width: 20px;
}

.unit-hex-container .item-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -20px;
  gap: 6px;
  z-index: 2;
}

.unit-hex-container-medium {
  position: relative;
  display: inline-flex;
  flex-direction: column;
}

.unit-hex-container-medium .star-container {
  margin-bottom: -16px;
  min-height: 18px;
}

.unit-hex-container-medium .star-container img {
  width: 18px;
  height: 18px;
}

.unit-hex-container-medium .item-container {
  margin-top: -24px;
  gap: 4px;
}

.unit-hex-container-medium .hexagon {
  width: 80px;
  height: 91px;
}

.unit-hex-container-medium .hexagon-in2 {
  background-size: 91px;
}

.unit-hex-container-medium .hex-border {
  width: 84px;
  height: 95px;
  position: absolute;
  top: 0px;
  left: -2px;
}

.grey-fill {
  background-color: $unit1 !important;
}

.green-fill {
  background-color: $unit2 !important;
}

.blue-fill {
  background-color: $unit3 !important;
}

.purple-fill {
  background-color: $unit4 !important;
}

.yellow-fill {
  background-color: $unit5 !important;
}

